	module.exports = {"data":{"markdownRemark":{"html":"<p>研究如何来写《革命后的政府对中国现代化的第二项探索》这本书，大概有了提纲。这本书我想集中谈谈我对中国政治和政府发展的认识，是一个比较有难度的题目，但也是一个有价值的题目，关键的问题是如何能够设计一个好的提纲。过去对这个问题有很长时间的研究，这次把各个论点结合起来，联结成一个整体。另外就是要发现新的观点和概念，一本好的书关键是有好的概念，现在定的轴心概念有三个：革命后社会、社会资源总量和社会质量。其他的部分围绕展开。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0087.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0087.md","frontmatter":{"title":"5月8日 星期日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0087.md absPath of file >>> MarkdownRemark"}}

